.recommendation-list-content {
    max-width: 1200px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(375px, 1fr));
    grid-column-gap: 20px;
    grid-row-gap: 30px;
    margin: 0 auto;
    padding: 50px 0px;
    height: auto;
}

.recommendation-list-container {
    width:100%;
    margin: 0 auto;
    text-align: center;
    height: auto;
    padding-top: 100px;
}
.recommendation {
    height: auto;
}

